n#header {\n  position: fixed;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  width: 300px;\n  transition: all ease-in-out 0.5s;\n  z-index: 9997;\n  transition: all 0.5s;\n  padding: 0 15px;\n  background: #040b14;\n  overflow-y: auto;\n}\n\n#header .profile img {\n  margin: 15px auto;\n  display: block;\n  width: 120px;\n  border: 8px solid #2c2f3f;\n}\n\n#header .profile h1 {\n  font-size: 24px;\n  margin: 0;\n  padding: 0;\n  font-weight: 600;\n  -moz-text-align-last: center;\n  text-align-last: center;\n  font-family: \"Poppins\", sans-serif;\n}\n\n#header .profile h1 a, #header .profile h1 a:hover {\n  color: #fff;\n  text-decoration: none;\n}\n\n#header .profile .social-links a {\n  font-size: 18px;\n  display: inline-block;\n  background: #212431;\n  color: #fff;\n  line-height: 1;\n  padding: 8px 0;\n  margin-right: 4px;\n  border-radius: 50%;\n  text-align: center;\n  width: 36px;\n  height: 36px;\n  transition: 0.3s;\n}\n\n#header .profile .social-links a:hover {\n  background: #601313;\n  color: #fff;\n  text-decoration: none;\n}\n\n#main {\n  margin-left: 300px;\n}\n\n@media (max-width: 1199px) {\n  #header {\n    left: -300px;\n  }\n  #main {\n    margin-left: 0;\n  }\n}\n\n/*--------------------------------------------------------------\n# Navigation Menu\n--------------------------------------------------------------*/\n/* Desktop Navigation */\n.nav-menu {\n  padding: 30px 0 0 0;\n}\n\n.nav-menu * {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n.nav-menu > ul > li {\n  position: relative;\n  white-space: nowrap;\n}\n\n.nav-menu a, .nav-menu a:focus {\n  display: flex;\n  align-items: center;\n  color: #a8a9b4;\n  padding: 12px 15px;\n  margin-bottom: 8px;\n  transition: 0.3s;\n  font-size: 15px;\n}\n\n.nav-menu a i, .nav-menu a:focus i {\n  font-size: 24px;\n  padding-right: 8px;\n  color: #6f7180;\n}\n\n.nav-menu a:hover, .nav-menu .active, .nav-menu .active:focus, .nav-menu li:hover > a {\n  text-decoration: none;\n  color: #fff;\n}\n\n.nav-menu a:hover i, .nav-menu .active i, .nav-menu .active:focus i, .nav-menu li:hover > a i {\n  color: #601313;\n}\n\n/* Mobile Navigation */\n.mobile-nav-toggle {\n  position: fixed;\n  right: 15px;\n  top: 15px;\n  z-index: 9998;\n  border: 0;\n  font-size: 24px;\n  transition: all 0.4s;\n  outline: none !important;\n  background-color: #601313;\n  color: #fff;\n  width: 40px;\n  height: 40px;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  line-height: 0;\n  border-radius: 50px;\n  cursor: pointer;\n}\n\n.mobile-nav-active {\n  overflow: hidden;\n}\n\n.mobile-nav-active #header {\n  left: 0;\n}\n\n/*--------------------------------------------------------------\n# Hero Section\n--------------------------------------------------------------*/\n#hero {\n  width: 100%;\n  height: 100vh;\n  background: url(\"../img/hero-bg-0.jpg\") top center;\n  background-size: cover;\n}\n\n#hero:before {\n  content: \"\";\n  background: rgba(5, 13, 24, 0.3);\n  position: absolute;\n  bottom: 0;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 1;\n}\n\n#hero .hero-container {\n  position: relative;\n  z-index: 2;\n  min-width: 300px;\n}\n\n#hero h1 {\n  margin: 0 0 10px 0;\n  font-size: 64px;\n  font-weight: 700;\n  line-height: 56px;\n  color: #fff;\n}\n\n#hero p {\n  color: #fff;\n  margin-bottom: 50px;\n  font-size: 26px;\n  font-family: \"Poppins\", sans-serif;\n}\n\n#hero p span {\n  color: #fff;\n  padding-bottom: 4px;\n  letter-spacing: 1px;\n  border-bottom: 3px solid #601313;\n}\n\n@media (min-width: 1024px) {\n  #hero {\n    background-attachment: fixed;\n  }\n}\n\n@media (max-width: 768px) {\n  #hero h1 {\n    font-size: 28px;\n    line-height: 36px;\n  }\n  #hero h2 {\n    font-size: 18px;\n    line-height: 24px;\n    margin-bottom: 30px;\n  }\n}\n\n/*--------------------------------------------------------------\n# Sections General\n--------------------------------------------------------------*/\nsection {\n  padding: 60px 0;\n  overflow: hidden;\n}\n\n.section-bg {\n  background: #f5f8fd;\n}\n\n.section-title {\n  padding-bottom: 30px;\n}\n\n.section-title h2 {\n  font-size: 32px;\n  font-weight: bold;\n  margin-bottom: 20px;\n  padding-bottom: 20px;\n  position: relative;\n  color: #173b6c;\n}\n\n.section-title h2::after {\n  content: '';\n  position: absolute;\n  display: block;\n  width: 50px;\n  height: 3px;\n  background: #601313;\n  bottom: 0;\n  left: 0;\n}\n\n.section-title p {\n  margin-bottom: 0;\n}\n\n/*--------------------------------------------------------------\n# About\n--------------------------------------------------------------*/\n.about .content h3 {\n  font-weight: 700;\n  font-size: 26px;\n  color: #173b6c;\n}\n\n.about .content ul {\n  list-style: none;\n  padding: 0;\n}\n\n.about .content ul li {\n  margin-bottom: 20px;\n  display: flex;\n  align-items: center;\n}\n\n.about .content ul strong {\n  margin-right: 10px;\n}\n\n.about .content ul i {\n  font-size: 16px;\n  margin-right: 5px;\n  color: #601313;\n  line-height: 0;\n}\n\n.about .content p:last-child {\n  margin-bottom: 0;\n}\n\n/*--------------------------------------------------------------\n# Facts\n--------------------------------------------------------------*/\n.facts {\n  padding-bottom: 30px;\n}\n\n.facts .count-box {\n  padding: 30px;\n  width: 100%;\n}\n\n.facts .count-box i {\n  display: block;\n  font-size: 44px;\n  color: #601313;\n  float: left;\n  line-height: 0;\n}\n\n\n\n.facts .count-box p {\n  padding: 15px 0 0 0;\n  margin: 0 0 0 60px;\n  font-family: \"Raleway\", sans-serif;\n  font-size: 14px;\n  color: #122f57;\n}\n\n.facts .count-box a {\n  font-weight: 600;\n  display: block;\n  margin-top: 20px;\n  color: #122f57;\n  font-size: 15px;\n  font-family: \"Poppins\", sans-serif;\n  transition: ease-in-out 0.3s;\n}\n\n.facts .count-box a:hover {\n  color: #1f5297;\n}\n\n/*--------------------------------------------------------------\n# Akills\n--------------------------------------------------------------*/\n.skills .progress {\n  height: 60px;\n  display: block;\n  background: none;\n  border-radius: 0;\n}\n\n.skills .progress .skill {\n  padding: 0;\n  margin: 0 0 6px 0;\n  text-transform: uppercase;\n  display: block;\n  font-weight: 600;\n  font-family: \"Poppins\", sans-serif;\n  color: #050d18;\n}\n\n.skills .progress .skill .val {\n  float: right;\n  font-style: normal;\n}\n\n.skills .progress-bar-wrap {\n  background: #dce8f8;\n  height: 10px;\n}\n\n.skills .progress-bar {\n  width: 1px;\n  height: 10px;\n  transition: .9s;\n  background-color: #601313;\n}\n\n/*--------------------------------------------------------------\n# Resume\n--------------------------------------------------------------*/\n.resume .resume-title {\n  font-size: 26px;\n  font-weight: 700;\n  margin-top: 20px;\n  margin-bottom: 20px;\n  color: #050d18;\n}\n\n.resume .resume-item {\n  padding: 0 0 20px 20px;\n  margin-top: -2px;\n  border-left: 2px solid #601313;\n  position: relative;\n}\n\n.resume .resume-item h4 {\n  line-height: 18px;\n  font-size: 18px;\n  font-weight: 600;\n  text-transform: uppercase;\n  font-family: \"Poppins\", sans-serif;\n  color: #050d18;\n  margin-bottom: 10px;\n}\n\n.resume .resume-item h5 {\n  font-size: 16px;\n  background: rgba(96, 19, 19, 0.3);\n  padding: 5px 15px;\n  display: inline-block;\n  font-weight: 600;\n  margin-bottom: 10px;\n}\n\n.resume .resume-item ul {\n  padding-left: 20px;\n}\n\n.resume .resume-item ul li {\n  padding-bottom: 10px;\n}\n\n.resume .resume-item:last-child {\n  padding-bottom: 0;\n}\n\n.resume .resume-item::before {\n  content: \"\";\n  position: absolute;\n  width: 16px;\n  height: 16px;\n  border-radius: 50px;\n  left: -9px;\n  top: 0;\n  background: #fff;\n  border: 2px solid #601313;\n}\n\n/*--------------------------------------------------------------\n# Portfolio\n--------------------------------------------------------------*/\n.portfolio .portfolio-item {\n  margin-bottom: 30px;\n}\n\n.portfolio #portfolio-flters {\n  padding: 0;\n  margin: 0 auto 35px auto;\n  list-style: none;\n  text-align: center;\n  background: #fff;\n  border-radius: 50px;\n  padding: 2px 15px;\n}\n\n.portfolio #portfolio-flters li {\n  cursor: pointer;\n  display: inline-block;\n  padding: 10px 15px 8px 15px;\n  font-size: 14px;\n  font-weight: 600;\n  line-height: 1;\n  text-transform: uppercase;\n  color: #272829;\n  margin-bottom: 5px;\n  transition: all 0.3s ease-in-out;\n}\n\n.portfolio #portfolio-flters li:hover, .portfolio #portfolio-flters li.filter-active {\n  color: #601313;\n}\n\n.portfolio #portfolio-flters li:last-child {\n  margin-right: 0;\n}\n\n.portfolio .portfolio-wrap {\n  transition: 0.3s;\n  position: relative;\n  overflow: hidden;\n  z-index: 1;\n}\n\n.portfolio .portfolio-wrap::before {\n  content: \"\";\n  background: rgba(255, 255, 255, 0.5);\n  position: absolute;\n  left: 0;\n  right: 0;\n  top: 0;\n  bottom: 0;\n  transition: all ease-in-out 0.3s;\n  z-index: 2;\n  opacity: 0;\n}\n\n.portfolio .portfolio-wrap .portfolio-links {\n  opacity: 1;\n  left: 0;\n  right: 0;\n  bottom: -60px;\n  z-index: 3;\n  position: absolute;\n  transition: all ease-in-out 0.3s;\n  display: flex;\n  justify-content: center;\n}\n\n.portfolio .portfolio-wrap .portfolio-links a {\n  color: #fff;\n  font-size: 28px;\n  text-align: center;\n  background: rgba(20, 157, 221, 0.75);\n  transition: 0.3s;\n  width: 50%;\n}\n\n.portfolio .portfolio-wrap .portfolio-links a:hover {\n  background: rgba(20, 157, 221, 0.95);\n}\n\n.portfolio .portfolio-wrap .portfolio-links a + a {\n  border-left: 1px solid #601313;\n}\n\n.portfolio .portfolio-wrap:hover::before {\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  opacity: 1;\n}\n\n.portfolio .portfolio-wrap:hover .portfolio-links {\n  opacity: 1;\n  bottom: 0;\n}\n\n/*--------------------------------------------------------------\n# Portfolio Details\n--------------------------------------------------------------*/\n.portfolio-details {\n  padding-top